import React from "react";
/* import AIBarGraphs from "./graphs/AIBarGraphs" */
import AIStackedGraphs from "./graphs/AIStackedGraphs";
import AIText from "./text/AIText";
import AINums from "./text/AInums";

/* function ai_bar_graph(props) {
  if (props.graphs.bar) {
    return <AIBarGraphs graphs={props.graphs.bar} />
  }
} */

function ai_detections(props) {
  if (props.graphs.stacked.detections) {
    return <AIStackedGraphs graphs={props.graphs.stacked.detections} />;
  }
}

function ai_text(props) {
  if (props.text) {
    return <AIText text={props.text} />;
  }
}

function ai_nums(props) {
  if (props.text) {
    return <AINums text={props.text} />;
  }
}

function AIMetrics(props) {
  if (props.areaTitle) {
    return (
      <h1 className="capitalize text-left font-bold text-xl text-gray-900 dark:text-gray-200 subpixel-antialiased">
        {props.areaTitle}
      </h1>
    );
  }

  return (
    <div id={props.boxName + "-metric"} className="grid gap-y-1 m-2">
      <div className="grid grid-cols-1">{ai_detections(props)}</div>

      <div className="grid">{ai_nums(props)}</div>
      <div className="grid">{ai_text(props)}</div>
    </div>
  );
}

export default AIMetrics;
