import React, { Component } from "react";
import PacMan from "../../loader/pacMan";
import axios from "axios";
import {
  Line,
  LineChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from "recharts";
import { config } from "../../../config/Settings";
import dayjs from "dayjs";
import { reverseCalcDates } from "../../../helpers/reverseCalcDates";

class LineGraph extends Component {
  constructor(props) {
    super(props);
    this.platform = this.props.platform;
    this.type = this.props.type;
    this.severity = this.props.severity;
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.getTotalImports = this.getTotalImports.bind(this);
  }

  componentDidMount() {
    this.getTotalImports();
    this.updateMetric = setInterval(
      this.getTotalImports,
      config.updateInterval
    );
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getTotalImports() {
    axios
      .get(`${config.logUrl}/api/logs/import/csbd`, {
        params: {
          platform: this.platform,
          type: this.type,
          severity: this.severity,
        },
      })
      .then(async (response) => {
        // Fetch warnings
        // Better practice would be to adapt the endpoint to fetch both errors and warnings at the same time
        await axios
          .get(`${config.logUrl}/api/logs/import/csbd`, {
            params: {
              platform: this.platform,
              type: this.type,
              severity: "WARN",
            },
          })
          .then((res) => {
            for (let i = 0; i < res?.data?.length; ++i) {
              response.data[i].warnings = res?.data[i]?.errors || 0;
            }
          })
          .catch((error) => {
            console.log("Error fetching warnings (LOL)", error);
            return null;
          });

        const reverse_result = reverseCalcDates(response.data);
        const logs = reverse_result.map((data) => {
          return {
            x: dayjs(data.date).format("YYYY-MM-DD"),
            y: parseInt(data.count),
            z: parseInt(data?.errors),
            z2: parseInt(data?.warnings) || 0,
          };
        });

        this.setState({ data: logs, loading: false, error: false });
      })
      .catch(() => {
        return this.setState({ error: true, loading: false });
      });
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <ResponsiveContainer width="100%" height={config.graphHeight}>
          <LineChart data={this.state.data}>
            <XAxis dataKey="x" stroke="#222" tick={false} />
            <YAxis dataKey="y" yAxisId="left" stroke="#222" />
            <YAxis
              dataKey="z"
              yAxisId="right"
              orientation="right"
              stroke="#222"
            />
            <YAxis
              dataKey="z2"
              yAxisId="right2"
              orientation="right"
              stroke="#222"
            />
            <Tooltip />
            <Line
              yAxisId="left"
              dataKey="y"
              name="Count"
              stroke={this.props.color}
              strokeWidth={5}
            />
            <Line
              yAxisId="right"
              dataKey="z"
              name="Errors"
              stroke="darkred"
              strokeWidth={2}
            />
            <Line
              yAxisId="right2"
              dataKey="z2"
              name="Warnings"
              stroke="grey"
              strokeWidth={2}
            />
          </LineChart>
        </ResponsiveContainer>
      );
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default LineGraph;
