import React, { Component } from "react";
import PacMan from "../../loader/pacMan";
import axios from "axios";
import { config } from "../../../config/Settings";

class DownloadStarted extends Component {
  constructor(props) {
    super(props);
    this.aibox_id = this.props.params.aibox_id;
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.getDownloadStarted = this.getDownloadStarted.bind(this);
  }

  componentDidMount() {
    this.getDownloadStarted();

    this.updateMetric = setInterval(
      this.getDownloadStarted,
      config.updateInterval
    );
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getDownloadStarted() {
    axios
      .get(
        `${config.spiUrl}/ai/que/count?filter=%7B%22state%22:%20%22download-started%22,%22aibox_id%22:%22${this.aibox_id}%22%7D&limit=9999999`
      )
      .then((response) => {
        this.setState({
          data: response.data || "0",
          loading: false,
          error: false,
        });
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false });
      });
  }

  activeItems() {
    const activeItems = this.state.data;
    if (activeItems > 8) {
      return {
        backgroundColor: "#F50010",
        color: "white",
        fontWeight: 900,
      };
    }
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <code className="text-center text-gray-900 dark:text-gray-200 flex-col content-end subpixel-antialiased">
          <p> Dl-started:</p>{" "}
          <p style={this.activeItems()}> {this.state.data} </p>
        </code>
      );
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default DownloadStarted;
