import React, { useState } from "react";
import DownloadFinished from "./ai/DownloadFinished";
import DownloadStarted from "./ai/DownloadStarted";
import AIStartedPerBox from "./ai/AIStartedPerBox";

function AINums(props) {
  const [content] = useState([...props.text]);

  return content.map((text, index) => {
    return (
      <div
        className="metric grid grid-cols-10"
        key={text.subTitle + "-" + index}
      >
        <div className="metric-header grid grid-flow-row auto-rows-min col-span-2">
          <h2 className="text-left font-semibold text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
            {text.params.aibox_id}
          </h2>
        </div>

        <div className="col-span-8 gap-y-2 grid grid-cols-3 grid-flow-col justify-center content-end">
          <DownloadStarted {...text} />
          <DownloadFinished {...text} />
          <AIStartedPerBox {...text} />
        </div>
      </div>
    );
  });
}

export default AINums;
