import React, { Component } from "react";
import PacMan from "../../loader/pacMan";
import axios from "axios";
import { config } from "../../../config/Settings";

class AIStarted extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.getAiStarted = this.getAiStarted.bind(this);
  }

  componentDidMount() {
    this.getAiStarted();

    this.updateMetric = setInterval(this.getAiStarted, config.updateInterval);
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getAiStarted() {
    axios
      .get(
        `${config.spiUrl}/ai/que/count?filter={%22state%22:%20%22ai-started%22}`
      )
      .then((response) => {
        this.setState({
          data: response.data || "0",
          loading: false,
          error: false,
        });
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false });
      });
  }

  activeItems() {
    const activeItems = this.state.data;
    if (activeItems > 13 && activeItems <= 17) {
      return {
        backgroundColor: "#EEDD33",
        fontWeight: 900,
      };
    } else if (activeItems > 17) {
      return {
        backgroundColor: "#F50010",
        color: "white",
        fontWeight: 900,
      };
    }
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <h1
          style={this.activeItems()}
          className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased"
        >
          Num. Active Items: {this.state.data}
        </h1>
      );
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default AIStarted;
