import React, { useState } from "react";
import AIDownloadFailed from "./ai/AIDownloadFailed";

function AIStackedGraphs(props) {
  const [stackedGraphs] = useState([...props.graphs]);

  return stackedGraphs.map((graph, index) => {
    return (
      <div>
        <div className="metric-header">
          <h2 className="capitalize text-left font-bold text-xl text-gray-900 dark:text-gray-200 subpixel-antialiased">
            {graph.subTitle}
          </h2>
        </div>
        <div
          className="metric grid col-span-full"
          key={graph.subTitle + "-" + index}
        >
          <div className="col-span-7">
            <AIDownloadFailed {...graph} />
          </div>
        </div>
      </div>
    );
  });
}

export default AIStackedGraphs;
