import React, { Component } from "react";
import PacMan from "../../loader/pacMan";
import axios from "axios";
import { config } from "../../../config/Settings";

class AIStartedPerBox extends Component {
  constructor(props) {
    super(props);
    this.aibox_id = this.props.params.aibox_id;
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.max = "";

    this.getAIStartedPerBox = this.getAIStartedPerBox.bind(this);
  }

  componentDidMount() {
    this.getAIStartedPerBox();

    this.updateMetric = setInterval(
      this.getAIStartedPerBox,
      config.updateInterval
    );
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getAIStartedPerBox() {
    axios
      .get(
        `${config.spiUrl}/ai/que/count?filter=%7B%22state%22:%20%22ai-started%22,%22aibox_id%22:%22${this.aibox_id}%22%7D&limit=9999999`
      )
      .then((response) => {
        this.setState({
          data: response.data || "0",
          loading: false,
          error: false,
        });
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false });
      });
  }
  activeItems(id) {
    let maxValue = 121213;
    switch (id) {
      case "aibox1":
        maxValue = 5;
        break;
      case "aibox2":
        maxValue = 5;
        break;
      case "aibox3":
        maxValue = 3;
        break;
      case "aibox4":
        maxValue = 2;
        break;
      default:
        maxValue = 0;
        break;
    }

    const activeItems = this.state.data;
    if (activeItems > maxValue) {
      return {
        backgroundColor: "#F50010",
        color: "white",
        fontWeight: 900,
      };
    }
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <code className="text-center text-gray-900 dark:text-gray-200 flex-col content-end subpixel-antialiased">
          <p> AI-started:</p>{" "}
          <p style={this.activeItems(this.aibox_id)}> {this.state.data} </p>
        </code>
      );
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default AIStartedPerBox;
