import React, { Component } from "react";
import PacMan from "../../loader/pacMan";
import axios from "axios";
import { config } from "../../../config/Settings";

class LatestLog extends Component {
  constructor(props) {
    super(props);
    this.aibox_id = this.props.params.aibox_id;
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.getLatestLog = this.getLatestLog.bind(this);
  }

  componentDidMount() {
    this.getLatestLog();

    this.updateMetric = setInterval(this.getLatestLog, 10_000);
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getLatestLog() {
    axios
      .get(`${config.logUrl}/api/logs/ai/latest_log`, {
        params: {
          aibox_id: this.aibox_id,
        },
      })
      .then((response) => {
        if (response?.data?.timestamp) {
          response.data.timestamp =
            new Date(response?.data?.timestamp).toLocaleString("da-DK", {
              hour12: false,
            }) || response?.data?.timestamp;
        }

        this.setState({
          data: response.data || "0",
          loading: false,
          error: false,
        });
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false });
      });
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return (
        <code>
          <p className="text-sm">
            {this.state.data.activity} / {this.state.data.sub_activity} /{" "}
            {this.state.data.timestamp}
          </p>
        </code>
      );
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default LatestLog;
