import React, { useState } from "react";
import LatestLog from "./ai/LatestLog";

function AIText(props) {
  const [content] = useState([...props.text]);

  return content.map((text, index) => {
    return (
      <div
        className="metric grid grid-cols-10"
        key={text.subTitle + "-" + index}
      >
        <div className="metric-header grid grid-flow-row auto-rows-min col-span-2">
          <h3 className="text-left font-semibold text-gray-900 dark:text-gray-200 subpixel-antialiased">
            {text.subTitle}
          </h3>
        </div>
        <div className="col-span-8 gap-y-2 justify-self-center">
          <LatestLog {...text} />
        </div>
      </div>
    );
  });
}

export default AIText;
