import React, { Component } from "react";
import PacMan from "../../loader/pacMan";
import axios from "axios";
import { config } from "../../../config/Settings";

class Runtime extends Component {
  constructor(props) {
    super(props);
    this.platform = this.props.platform;
    this.platformOverride = this.props.platformOverride || null;
    this.type = this.props.type;
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.getRuntime = this.getRuntime.bind(this);
  }

  componentDidMount() {
    this.getRuntime();

    this.updateMetric = setInterval(this.getRuntime, config.updateInterval);
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getRuntime() {
    axios
      .get(`${config.logUrl}/api/logs/import/runtime`, {
        params: {
          platform: this.platformOverride || this.platform,
          type: this.type,
        },
      })
      .then((response) => {
        this.setState({ data: response.data, loading: false, error: false });
      })
      .catch((e) => {
        return this.setState({ error: e, loading: false });
      });
  }

  philipMethod() {
    const barHeight = "20px";

    return (
      <div
        className="health_container"
        style={{
          width: "100px",
          height: barHeight,
          position: "relative",
          background:
            "linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(255,255,0,1) 25%, rgba(26,255,0,1) 100%)",
          display: "flex",
          alignItems: "center",
        }}
      >
        <div
          className="health_bar"
          style={{
            height: barHeight,
            width: `${this.state.data.runtime}%`,
            position: "absolute",
            right: 0,
            top: 0,
            backgroundColor: "lightgrey",
            zIndex: 1,
          }}
        />

        <p
          className="subpixel-antialiased"
          style={{ zIndex: 2, paddingLeft: "20px", color: "rebeccapurple" }}
        >
          {this.state.data.sleep_time}%
        </p>
      </div>
    );
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return this.philipMethod();
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default Runtime;
